import React, {useCallback, useEffect, useState, useMemo} from 'react';
import { useSelector, useDispatch } from "react-redux";
import SVG from "react-inlinesvg";
import { ETHER } from "@uniswap/sdk";
import {Button, Row, Col} from 'react-bootstrap';
import {useActiveWeb3React} from "../../hooks";
import PlatformLogo from "../PlatformLogo";
import styled from "styled-components";
import {getGasPrice} from "../../state/currency/actions";
import GasPrice from "../GasPrice";
import { usePoolApproveCallback } from '../../state/pools/hooks';
import { ApprovalState } from '../../constants';
import GradientButton from '../UI/Button';

import { Modal } from '../Modal/bootstrap';
import { TransactionResponse } from '@ethersproject/providers'
import { RouteComponentProps } from 'react-router-dom'
import { Text } from 'rebass'
import { AutoColumn } from '../Column'
import { ConfirmationModalContent, ConfirmationPendingContent, TransactionSubmittedContent } from '../TransactionConfirmationModal'

import {RowBetween, RowFixed} from '../Row'
import { AppState } from '../../state';
import useTransactionDeadline from '../../hooks/useTransactionDeadline'

import { useTransactionAdder } from '../../state/transactions/hooks'
import {useUserSlippageTolerance} from '../../state/user/hooks'
import { TYPE } from '../../theme'
import {getContract, isAddress, calculateGasMargin} from '../../utils'
import CurrencyLogo from "../CurrencyLogo";
import { REMOVE_CONTRACTS as CONTRACTS } from '../../constants';
import curvePipeABI from '../../constants/abis/removeLiquidity/curve.json';
import balancerPipeABI from '../../constants/abis/removeLiquidity/balancer.json';
import yVaultPipeABI from '../../constants/abis/removeLiquidity/yVault.json';
import uniswapPipeABI from '../../constants/abis/removeLiquidity/uniswap.json';
import {clearSelectedPool} from "../../state/pools/actions";
import PoolInput from '../PoolInput';
import {usePoolBalance} from "../../state/pools/hooks";
import ERC20_ABI from '../../constants/abis/erc20.json'
import { BigNumber } from 'ethers';
import {
    AccountState,
    AccountStateContent,
    AccountStateDesc,
    AccountStateTitle,
    PriceTopbar
} from "../AddLiquidityModal/uniswap";
import {useWalletModalToggle} from "../../state/application/hooks";
import {LightCard} from "../StyledCards";
import {PlatformRow, PlatformTitle} from "../AddLiquidityModal";
import {useTranslation} from "react-i18next";


export const Dots = styled.span`
  &::after {
    display: inline-block;
    animation: ellipsis 1.25s infinite;
    content: '.';
    width: 1em;
    text-align: left;
  }
  @keyframes ellipsis {
    0% {
      content: '.';
    }
    33% {
      content: '..';
    }
    66% {
      content: '...';
    }
  }
`


export const ResponsivePlatformTitle = styled(PlatformTitle)`
  padding-left: 10px;
`

export const ConfirmationText = styled(Text)`
  font-size: 48px;
  line-height: 42px;
  font-weight: 500;
  
  @media (max-width: 1199px) {
    font-size: 24px;
    font-weight: 700;
  }
`

const HeaderAutoColumn = styled(AutoColumn)`
  grid-row-gap: 20px;
  
  @media (max-width: 1199px) {
    grid-row-gap: 10px;
  }
`

const HeaderCurrencyText = styled(Text)`
  font-size: 1.5rem;
  
  @media (max-width: 1199px) {
    font-size: 1rem;
  }
`

export const PlatformLabel = styled.span`
  display: block;
  color: ${({ theme }) => theme.text1};
  font-weight: 400;
  font-size: .875rem;
  padding: 0 ;
  margin: 1rem 0;
  
  @media (min-width: 768px) {
    padding: 0 1.5rem;
  }
`

export const PlatformControl = styled.div`
  background-color: ${({ theme }) => theme.bg3};
  border-radius: 18px;
  padding: 15px 10px;
  min-height: 56px;
  display: flex;
  align-items: center;
  margin-bottom: 60px;
`

export default function({
    history,
}: RouteComponentProps)  {
    
    const { account, chainId, library } = useActiveWeb3React()
    const toggleWalletModal = useWalletModalToggle();
    const { t } = useTranslation()

    const BUTTON_STATES = useMemo(() => {
        return {
            'not_started': t('instantSwap.not_started'),
            'initializing': t('instantSwap.initializing'),
            'allowance': t('instantSwap.allowance'),
            'approving': t('instantSwap.approving'),
            'validation': t('instantSwap.validation'),
            'create_tx': t('instantSwap.create_tx'),
            'send_tx': t('instantSwap.send_tx'),
            'submitted': t('instantSwap.submitted'),
            'pending': t('instantSwap.pending'),
            'failed': t('instantSwap.failed'),
        }
    }, [t])

    const dispatch = useDispatch();
    const [amount, setAmount] = useState('');
    const [buttonState, setButtonState] = useState(BUTTON_STATES.not_started)
    const {gasPrice, selectedGasPrice} = useSelector((state: AppState) => state.currency);
    const selectedPool = useSelector((state: AppState) => state.pools.selected)
    const pool = selectedPool.data;
    const type = selectedPool.type;
    const selectedCurrencyBalance = usePoolBalance(account ?? undefined, pool.address ?? undefined)
    const getSpender = useMemo(() => {
        let spender,
            abi;
        if(type && type.toLowerCase() === 'curve') {
            spender = CONTRACTS.curve
            abi = curvePipeABI
        } else if(type && type.toLowerCase() === 'balancer') {
            spender = CONTRACTS.balancer
            abi = balancerPipeABI
        } else if(type && type.toLowerCase() === 'yearn') {
            spender = CONTRACTS.yVault
            abi = yVaultPipeABI
        }else if(type && type.toLowerCase() === 'uniswap') {
            spender = CONTRACTS.uniswap
            abi = uniswapPipeABI
        } else {
            return ['', undefined];
        }
        return [spender, abi];
    }, [type])

    const [spender] = getSpender;
    const [approve, approveCallback] = usePoolApproveCallback(pool, amount, spender);

    useEffect(() => {
        if(!pool || !isAddress(pool.address)) {
            dispatch(clearSelectedPool());
            history.push('/invest/pools');
        }
    }, [pool, dispatch, history])

    // modal and loading
    const [showConfirm, setShowConfirm] = useState<boolean>(false)
    const [attemptingTxn, setAttemptingTxn] = useState<boolean>(false) // clicked confirm

    // txn values
    const deadline = useTransactionDeadline() // custom from users settings
    const [allowedSlippage] = useUserSlippageTolerance() // custom from users
    const [txHash, setTxHash] = useState<string>('')

    const addTransaction = useTransactionAdder()

    const setFailedTxn = () => {
        setButtonState(BUTTON_STATES.failed);

        setTimeout(() => {
            setButtonState(BUTTON_STATES.not_started);
        }, 3000)
    }

    const getApprove = () => {
        if(approveCallback) {
            // @ts-ignore
            approveCallback();
        }
    }

    async function onRemove() {
        if (!chainId || !library || !account) return
        let router;
        let [spender, abi] = getSpender;
        if(!abi) {
            return;
        } else {
            // @ts-ignore
            router = getContract(spender, abi, library, account);
        }
        setButtonState(BUTTON_STATES.initializing);
        const erc20_contract = getContract(pool.address, ERC20_ABI, library, account);
        
        setButtonState(BUTTON_STATES.allowance);
        const allowance = await erc20_contract.functions.allowance(account, spender);
        
        
        // @ts-ignore
        const parsedAmountA: string = `${amount * (10 ** 18)}`
        if (!parsedAmountA || !pool || !deadline) {
            return
        }
        let canTx = false;

        if(Number(allowance[0].toString()) < Number(parsedAmountA)) {
            setButtonState(BUTTON_STATES.approving);
            try {
                const tx = await erc20_contract.functions.approve(spender, parsedAmountA);
                if(tx) {
                    canTx = true;
                }
            } catch(e) {
                setFailedTxn();
            }
        } else {
            canTx = true;
        }
    
        if(!canTx) {
            return ;
        }
        
        setButtonState(BUTTON_STATES.create_tx);

        let method: (...args: any) => Promise<TransactionResponse>,
            args: Array<string | string[] | number>
        let estimate = type === 'Balancer' ? router.estimateGas.EasyZapOut : router.estimateGas.ZapOut
        method = type === 'Balancer' ? router.EasyZapOut : router.ZapOut
        args = []
        if(type && type.toLowerCase() === 'curve') {
            args = [
                account,
                pool.address,
                parsedAmountA,
                '0x0000000000000000000000000000000000000000',
                0,
            ]
        } else if(type && type.toLowerCase() === 'balancer') {
            args = [
                '0x0000000000000000000000000000000000000000',
                pool.address,
                parsedAmountA,
                0,
            ]
        } else if(type && type.toLowerCase() === 'yearn') {
            args = [
                account,
                '0x0000000000000000000000000000000000000000',
                pool.address,
                2,
                parsedAmountA,
                0,
            ]
        } else {
            args = [
                '0x0000000000000000000000000000000000000000',
                pool.address,
                parsedAmountA,
                0,
            ]
        }

        
        setAttemptingTxn(true)

        setButtonState(BUTTON_STATES.send_tx);
        const selectedGas = gasPrice.find(item => item[0] === selectedGasPrice);
        let gasEstimatedPrice = 0;
        if(selectedGas) {
            gasEstimatedPrice = selectedGas[1] * (10 ** 9);
        }

        const gas = await estimate(...args)
                            .catch(e => {
                                setFailedTxn()
                                setAttemptingTxn(false)
                            })
        await method(...args, {
                gasLimit: calculateGasMargin(gas || BigNumber.from(0)),
                gasPrice: gasEstimatedPrice,
            }).then(response => {
                
                setButtonState(BUTTON_STATES.submitted);
                setAttemptingTxn(false)

                addTransaction(response, {
                    summary:
                        'Withdraw ' +
                        parsedAmountA +
                        ' ' +
                        pool?.poolName,
                })

                setTimeout(() => {
                    setButtonState(BUTTON_STATES.not_started);
                }, 3000)
                setTxHash(response.hash)

            })
            .catch(error => {
                setAttemptingTxn(false)
                setFailedTxn()
                // we only care if the error is something _other_ than the user rejected the tx
                if (error?.code !== 4001) {
                    console.error(error)
                }
            })
    }

    useEffect(() => {
        dispatch(getGasPrice());
    }, [dispatch])

    const modalHeader = () => {
        return  (
            <HeaderAutoColumn>
                <div className={'d-flex align-items-center align-items-xl-end'}>
                    <ConfirmationText marginRight={10}>
                        {amount}
                    </ConfirmationText>
                    <div className={'d-flex align-items-center'}>
                        <PlatformLogo platform={type} name={pool?.poolName} size={36}/>
                    </div>
                </div>
                <HeaderCurrencyText>
                    {pool?.poolName + ' Pool Tokens'}
                </HeaderCurrencyText>
                <TYPE.italic fontSize={12} textAlign="left" padding={'8px 0 0 0 '}>
                    {`Output is estimated. If the price changes by more than ${allowedSlippage /
                    100}% your transaction will revert.`}
                </TYPE.italic>
            </HeaderAutoColumn>
        )
    }

    const modalBottom = () => {
        return (
            <>
                <RowBetween>
                    <TYPE.body>{pool?.poolName} Pool Token</TYPE.body>
                    <RowFixed>
                        <PlatformLogo platform={type} name={pool?.poolName} size={24} style={{ marginRight: '8px' }} />
                    </RowFixed>
                </RowBetween>
                <RowBetween>
                    <TYPE.body>Ethereum Output</TYPE.body>
                    <RowFixed>
                        <CurrencyLogo currency={ETHER} size={'24px'}/>
                    </RowFixed>
                </RowBetween>
                <Button variant={'primary'} className={'py-3 font-weight-bolder font-size-lg'} style={{ margin: '20px 0 0 0', maxHeight: 56 }} onClick={onRemove}>
                    {buttonState}
                </Button>
            </>
        )
    }

    const hideModal = () => {
        dispatch(clearSelectedPool());
        history.push('/invest/pools');
    }

    const pendingText = `Withdrawing ${amount} ${
        pool?.poolName
    }`

    const handleDismissConfirmation = useCallback(() => {
        setShowConfirm(false)
        setTxHash('')
    }, [txHash])

    const onFieldAInput = useCallback((value) => {
        setAmount(value);
    }, [amount])

    return (
        <>
            <Modal
                show={true}
                onHide={hideModal}
                dialogClassName={'custom-modal'}
                backdropClassName={'backdrop'}
                size={'lg'}
                centered={true}>
                <Modal.Body style={{ padding: !showConfirm ? '30px' : '0' }}>
                    
                {!account ? (

                    <Row>
                        <Col xs={12}>
                            <AccountState type={'danger'} className={'bg-light-danger'}>
                                <SVG src={require('../../assets/images/account/wallet.svg').default} width={36} height={36}/>
                                <AccountStateContent>
                                    <AccountStateTitle className={'mb-0'}>{t('wallet.notConnected')}</AccountStateTitle>
                                </AccountStateContent>
                            </AccountState>
                        </Col>
                        <Col xs={12} className={'d-flex align-items-center justify-content-center'} style={{ padding: '80px 0 88px' }}>
                            <GradientButton
                                className={'btn-lg'}
                                onClick={toggleWalletModal}
                            >
                                {t('wallet.connect')}
                            </GradientButton>
                        </Col>
                    </Row>
                ) : !showConfirm ? (
                    <Row>
                        <Col xs={12}>
                            <AccountState type={'success'} className={'bg-light-success d-none d-xl-flex'}>
                                <SVG src={require('../../assets/images/account/wallet.svg').default} width={36} height={36}/>
                                <AccountStateContent>
                                    <AccountStateTitle>{t('wallet.connected')}</AccountStateTitle>
                                    <AccountStateDesc>{t('wallet.connectedTo')} <strong>{account}</strong></AccountStateDesc>
                                </AccountStateContent>
                            </AccountState>
                        </Col>
                        <Col xs={12} className={'gutter-b'}>
                            <PoolInput
                                value={amount}
                                onUserInput={onFieldAInput}
                                onMax={() => {
                                    onFieldAInput(selectedCurrencyBalance ?? '')
                                }}
                                type={type}
                                disableCurrencySelect={true}
                                showMaxButton={true}
                                pool={pool}
                                label={t('pools.selectedPool')}
                                id="pool-input"
                            />
                        </Col>
                        <Col xs={12} className={'d-none d-xl-flex'}>

                            <LightCard padding={'0'} borderRadius={'18px'} style={{ marginBottom: 20 }}>
                                <PriceTopbar>
                                    {t('pools.willReceive')}
                                </PriceTopbar>

                                <PlatformRow>
                                    <CurrencyLogo currency={ETHER} size={'32px'} />
                                    <PlatformTitle>Ethereum</PlatformTitle>
                                </PlatformRow>
                            </LightCard>
                        </Col>

                        <Col xs={12} className={'d-flex d-xl-none flex-column'}>
                            <div className={'d-flex flex-column'}>
                                <PlatformLabel>
                                    {t('pools.willReceive')}
                                </PlatformLabel>

                                <PlatformControl>
                                    <CurrencyLogo currency={ETHER} size={'24px'} />
                                    <ResponsivePlatformTitle>Ethereum</ResponsivePlatformTitle>
                                </PlatformControl>
                            </div>
                        </Col>

                        <Col xs={12} className={'gutter-b'}>
                            <LightCard padding={'0'} borderRadius={'18px'} style={{ marginBottom: 20 }}>
                                <PriceTopbar>
                                    {t('pools.selectGasSetting')}
                                </PriceTopbar>
                                <GasPrice gasList={gasPrice} selected={selectedGasPrice}/>
                            </LightCard>
                        </Col>
                        <Col xs={12} className={'d-flex flex-column align-items-stretch align-items-xl-center justify-content-center'}  style={{ paddingTop: 30 }} >
                            {!account ? (
                                <Button
                                    style={{ minWidth: 250 }}
                                    variant={'outline-primary'} disabled className={'py-3 font-size-lg font-weight-bolder'}>Connect to your wallet</Button>
                            ) : (
                                <Button
                                    onClick={() => {
                                        if(approve === ApprovalState.NOT_APPROVED || approve === ApprovalState.UNKNOWN) {
                                            getApprove();
                                        } else if(approve === ApprovalState.PENDING) {
                                            return ;
                                        } else {
                                            setShowConfirm(true)
                                        }                                       
                                    }}
                                    style={{ minWidth: 250 }}
                                    disabled={!amount || Number(selectedCurrencyBalance) < Number(amount) || approve === ApprovalState.PENDING || Number(amount) === 0}
                                    variant={!amount || Number(selectedCurrencyBalance) < Number(amount)? 'outline-primary' : 'primary'}
                                    className={`${!!amount || Number(selectedCurrencyBalance) >= Number(amount) || Number(amount) === 0? 'outline-primary' : ''} py-3`}
                                >
                                    <span className="font-weight-bold font-size-lg">
                                        {!amount || Number(amount) === 0 ? t('exchange.enterAmount') :
                                           Number(selectedCurrencyBalance) < Number(amount) ? t('insufficientBalance') :
                                           approve === ApprovalState.NOT_APPROVED || approve === ApprovalState.UNKNOWN ? t('approve') :
                                           approve === ApprovalState.PENDING ? t('pending') : t('pools.withdraw')}
                                    </span>

                                </Button>
                            )}

                        </Col>
                    </Row>
                ) : attemptingTxn ? (
                    <ConfirmationPendingContent onDismiss={handleDismissConfirmation} pendingText={pendingText} />
                ) : txHash ? (
                    <TransactionSubmittedContent chainId={chainId} hash={txHash} onDismiss={handleDismissConfirmation} />
                ) : (
                    <ConfirmationModalContent
                        title={t('pools.willPay')}
                        onDismiss={handleDismissConfirmation}
                        topContent={modalHeader}
                        bottomContent={modalBottom}
                    />
                )}
                </Modal.Body>
            </Modal>
        </>
    )
};